"use client"

import { useEffect, useRef, useState } from "react"
import { useRouter } from "next/navigation"
import { gsap } from "gsap"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Video, VideoOff, Mic, MicOff, Settings } from "lucide-react"

export default function LobbyPage() {
  const router = useRouter()
  const containerRef = useRef<HTMLDivElement>(null)
  const cardRef = useRef<HTMLDivElement>(null)
  const videoRef = useRef<HTMLVideoElement>(null)
  const [meetingCode, setMeetingCode] = useState("")
  const [isVideoEnabled, setIsVideoEnabled] = useState(true)
  const [isAudioEnabled, setIsAudioEnabled] = useState(true)
  const [stream, setStream] = useState<MediaStream | null>(null)

  useEffect(() => {
    const ctx = gsap.context(() => {
      gsap.from(containerRef.current, {
        opacity: 0,
        duration: 0.8,
        ease: "power2.out",
      })

      gsap.from(cardRef.current, {
        y: 50,
        opacity: 0,
        duration: 1,
        delay: 0.3,
        ease: "power3.out",
      })
    })

    return () => ctx.revert()
  }, [])

  useEffect(() => {
    async function setupCamera() {
      try {
        const mediaStream = await navigator.mediaDevices.getUserMedia({
          video: true,
          audio: true,
        })
        setStream(mediaStream)
        if (videoRef.current) {
          videoRef.current.srcObject = mediaStream
        }
      } catch (error) {
        console.error("[v0] Error accessing camera:", error)
      }
    }

    setupCamera()

    return () => {
      if (stream) {
        stream.getTracks().forEach((track) => track.stop())
      }
    }
  }, [])

  useEffect(() => {
    if (stream && videoRef.current) {
      const videoTrack = stream.getVideoTracks()[0]
      if (videoTrack) {
        videoTrack.enabled = isVideoEnabled
      }
    }
  }, [isVideoEnabled, stream])

  useEffect(() => {
    if (stream) {
      const audioTrack = stream.getAudioTracks()[0]
      if (audioTrack) {
        audioTrack.enabled = isAudioEnabled
      }
    }
  }, [isAudioEnabled, stream])

  const handleJoinMeeting = () => {
    if (meetingCode.trim()) {
      router.push(`/meeting/${meetingCode}`)
    }
  }

  const handleNewMeeting = () => {
    const newCode = Math.random().toString(36).substring(2, 10)
    router.push(`/meeting/${newCode}`)
  }

  return (
    <>
      <div ref={containerRef} className="min-h-screen flex items-center justify-center p-4 relative z-10">
        <Card ref={cardRef} className="w-full max-w-4xl p-8 backdrop-blur-xl bg-card/80 border-border/50 shadow-2xl">
          <div className="grid md:grid-cols-2 gap-8">
            {/* Video Preview */}
            <div className="space-y-4">
              <h2 className="text-2xl font-bold text-balance">Ready to join?</h2>
              <div className="relative aspect-video bg-muted rounded-lg overflow-hidden">
                {isVideoEnabled ? (
                  <video
                    ref={videoRef}
                    autoPlay
                    playsInline
                    muted
                    className="w-full h-full object-cover scale-x-[-1]"
                  />
                ) : (
                  <div className="w-full h-full flex items-center justify-center bg-muted">
                    <div className="w-20 h-20 rounded-full bg-primary flex items-center justify-center">
                      <span className="text-3xl font-bold text-primary-foreground">
                        {meetingCode ? meetingCode[0].toUpperCase() : "U"}
                      </span>
                    </div>
                  </div>
                )}
              </div>

              {/* Controls */}
              <div className="flex items-center justify-center gap-3">
                <Button
                  size="icon"
                  variant={isVideoEnabled ? "default" : "destructive"}
                  onClick={() => setIsVideoEnabled(!isVideoEnabled)}
                  className="rounded-full h-12 w-12"
                >
                  {isVideoEnabled ? <Video className="h-5 w-5" /> : <VideoOff className="h-5 w-5" />}
                </Button>
                <Button
                  size="icon"
                  variant={isAudioEnabled ? "default" : "destructive"}
                  onClick={() => setIsAudioEnabled(!isAudioEnabled)}
                  className="rounded-full h-12 w-12"
                >
                  {isAudioEnabled ? <Mic className="h-5 w-5" /> : <MicOff className="h-5 w-5" />}
                </Button>
                <Button size="icon" variant="outline" className="rounded-full h-12 w-12 bg-transparent">
                  <Settings className="h-5 w-5" />
                </Button>
              </div>
            </div>

            {/* Meeting Options */}
            <div className="space-y-6 flex flex-col justify-center">
              <div className="space-y-3">
                <h3 className="text-lg font-semibold">Join a meeting</h3>
                <div className="flex gap-2">
                  <Input
                    placeholder="Enter meeting code"
                    value={meetingCode}
                    onChange={(e) => setMeetingCode(e.target.value)}
                    onKeyDown={(e) => e.key === "Enter" && handleJoinMeeting()}
                    className="flex-1"
                  />
                  <Button onClick={handleJoinMeeting} disabled={!meetingCode.trim()}>
                    Join
                  </Button>
                </div>
              </div>

              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <span className="w-full border-t border-border" />
                </div>
                <div className="relative flex justify-center text-xs uppercase">
                  <span className="bg-card px-2 text-muted-foreground">Or</span>
                </div>
              </div>

              <div className="space-y-3">
                <h3 className="text-lg font-semibold">Start a new meeting</h3>
                <Button onClick={handleNewMeeting} className="w-full" size="lg">
                  Create Meeting
                </Button>
              </div>
            </div>
          </div>
        </Card>
      </div>
    </>
  )
}
